    //   green: '#99C794',
    //   grey: '#A7ADBA',
    // };

    // const alertClass = {
    //   red: "map-red",
    //   amber: "map-amber",
    //   green: "map-green",
    // };

    // return this.props.locations.map( (loc, index) => {

    //   let found = '';
    //   // let foundDate = '';
    //   // let locationHistory = this.props.alerts.map( (alert, i) => {

    //   //   let alertLevel = alertClass[alert.alert] || '';
    //   //   if (alert.name === loc.name) {
    //   //     if (found === '') {
    //   //       found = alert.alert;
    //   //       foundDate = alert.date.format("Do MMM YYYY");
    //   //     }
    //   //     return (
    //   //       <span key={i} className="flex">
    //   //         <span className="no-padding flex-ible">{alert.date.format("YYYY-MM-DD")}</span>
    //   //         <span className={`no-padding ${alertLevel}`}>{alert.alert}</span>
    //   //       </span>
    //   //     );
